.header {
  min-height: 650px;

  &__rectangle {
    background-image: url(../images/Rectangle.png);
    background-repeat: no-repeat;
    width: 90%;
    height: 466px;
    display: none;

    @include on-tablet {
      display: block;
      margin-left: -34px;
    }

    @include on-desktop {
      background-image: url(../images/Rectangle2.png);
      margin-left: -135px;
    }
  }

  &__wrapper {
    position: relative;
  }

  &__content {
    padding-top: 74px;

    @include on-tablet {
      margin-top: -420px;
    }

    @include on-desktop {
      padding-top: 100px;
    }
  }

  &__photo {
    margin-inline: -30px;
    transition: 0.3s;
    cursor: pointer;

    @include on-tablet {
      margin: 0;
      position: absolute;
      right: 0;
      width: 45%;
    }
  }

  &__foto {
    transition: 0.3s;

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__foto-mb {
    width: 100%;
  }

  &__foto-desk {
    display: none;
  }

  &__description {
    width: 100%;

    @include on-tablet {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      width: 55%;
    }

    @include on-desktop {
      width: 50%;
    }
  }

  &__title {
    font-family: Bebas Neue, sans-serif;
    font-size: 36px;
    font-weight: 400;
    line-height: 43px;
    letter-spacing: 0.155em;
    text-align: left;

    @include on-tablet {
      margin: 0;
      position: relative;
      width: 70%;
      font-family: Bebas Neue, sans-serif;
      font-size: 64px;
      font-weight: 400;
      line-height: 64px;
      letter-spacing: 0.155em;
      text-align: left;
    }

    @include on-desktop {
      padding-left: 60px;
      width: 45%;
    }
  }

  &__title-transparent {
    color: transparent;
    -webkit-text-stroke: 0.68px #03839e;
  }

  &__title-colored {
    color: #03839e;
  }

  &__value {
    font-family: Manrope, sans-serif;
    font-size: 26px;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0em;
    color: #333;
    text-align: left;
  }

  &__price {
    width: 50%;

    @include on-desktop {
      width: 27%;
    }
  }

  &__price-description {
    margin: 0;
    font-family: Manrope, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0em;
    color: #4f4f4f;
    text-align: left;
  }

  &__link {
    margin-top: 400px;
    transform: rotate(90deg);
    position: absolute;
    top: 235px;
    right: -80px;
    font-family: Manrope, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0em;
    text-decoration: none;
    color: #03839e;
    transition: 0.3s;

    &::after {
      content: '';
      background-image: url(../images/icons/Arrow.png);
      background-repeat: no-repeat;
      width: 10px;
      height: 48px;
      position: absolute;
      transform: rotate(-90deg);
      right: -45px;
      top: -16px;
      transition: 0.3s;
    }

    &:hover {
      color: #444444;
      transition: 0.3s;
    }

    @include on-tablet {
      top: 75px;
      left: -60px;
      right: auto;
    }
  }

  &__button {
    margin-top: 20px;
    width: 100%;
    height: 48px;
    border: 1px;
    background-color: #ffe339;
    background-image: url(../images/icons/header-button.png);
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background-color: #ffe339;
      transition: 0.3s;
    }

    @include on-tablet {
      margin: 40px 30px 60px;
      width: 240px;
    }

    @include on-desktop {
      margin: 40px 0;
    }
  }
}
