.footer {
  margin: 110px 0 40px;

  @include on-desktop {
    margin-top: 180px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    @include on-tablet {
      position: relative;
      flex-direction: row;
      align-items: flex-start;
    }
  }

  &__cocials {
    display: flex;
    justify-content: space-between;
    margin-bottom: 44px;

    @include on-tablet {
      margin-bottom: 30px;
    }

    @include on-desktop {
      margin-bottom: 90px;
    }
  }

  &__logo {
    margin-bottom: 20px;
  }

  &__icons {
    width: 230px;
    text-align: center;
  }

  &__icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    padding: 10px;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }
  }

  &__facebook {
    background-image: url(../images/footer/facebook-mb.png);
    background-repeat: no-repeat;

    @include on-desktop {
      background-image: url(../images/footer/facebook-ds.png);
    }
  }

  &__twitter {
    background-image: url(../images/footer/twitter-mb.png);
    background-repeat: no-repeat;

    @include on-desktop {
      background-image: url(../images/footer/twitter-ds.png);
    }
  }

  &__insta {
    background-image: url(../images/footer/insta-mb.png);
    background-repeat: no-repeat;

    @include on-desktop {
      background-image: url(../images/footer/insta-ds.png);
    }
  }

  &__adress {
    display: none;

    @include on-tablet {
      display: block;
      width: 160px;
    }
  }

  &__about {
    display: none;

    @include on-tablet {
      display: block;
      width: 130px;
    }
  }

  &__title {
    font-family: Bebas Neue, sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #000;
    text-decoration: none;
    margin: 0 0 16px;
  }

  &__about-title {
    margin: 0;
  }

  &__title-link {
    &:hover {
      color: blue;
    }
  }

  &__adress-data {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: #000;
    text-decoration: none;

    &:hover {
      color: blue;
    }
  }

  &__end-mark {
   @include on-tablet {
    position: absolute;
    right: 0;
    top: 36px;
   }
  }
}
