.menu {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 72%;
  opacity: 0;
  pointer-events: none;
  background-color: #03839e;
  transition: 0.3s;
  z-index: 9;

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__icon {
    background-image: url(../images/icons/close-menu.png);
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    bottom: 35px;
    right: 30px;
    width: 12px;
    height: 12px;
    transition: 0.3s;

    &:hover {
      transform: rotate(90deg);
      transition: 0.3s;
    }
  }

  &__list {
    padding-left: 35%;
    padding-top: 120px;
  }

  &__item {
    margin-bottom: 10px;
    list-style: none;
  }

  &__link {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #f8f8f8;
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      border-bottom: 1px solid #f8f8f8;
      transition: 0.3s;
    }
  }

  &__basket-link {
    &::after {
      content: '0';
      position: absolute;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: red;
      color: #f8f8f8;
      font-family: Bebas Neue, sans-serif;
      font-size: 9px;
      font-weight: 400;
      line-height: 11px;
      letter-spacing: 0.155em;
      text-align: center;
      padding-top: 3px;
      margin-left: 8px;
    }
  }

  &__side-mark {
    position: absolute;
    transform: rotate(-90deg);
    right: 16px;
    bottom: 130px;
    font-family: Manrope, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0em;
    text-align: left;
    color: #f8f8f8;
    text-transform: uppercase;

    &::before {
      content: '';
      position: absolute;
      transform: rotate(90deg);
      top: -12px;
      left: -26px;
      width: 1px;
      height: 44px;
      background-color: #f8f8f8;
    }
  }
}
