.menu {
  opacity: 0;
  pointer-events: none;
  z-index: 9;
  background-color: #03839e;
  width: 72%;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  right: 0;
}

.menu:target {
  opacity: 1;
  pointer-events: all;
}

.menu__icon {
  background-image: url("close-menu.d0081a8d.png");
  background-repeat: no-repeat;
  width: 12px;
  height: 12px;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: 35px;
  right: 30px;
}

.menu__icon:hover {
  transition: all .3s;
  transform: rotate(90deg);
}

.menu__list {
  padding-top: 120px;
  padding-left: 35%;
}

.menu__item {
  margin-bottom: 10px;
  list-style: none;
}

.menu__link {
  letter-spacing: 0;
  text-align: left;
  color: #f8f8f8;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-decoration: none;
  transition: all .3s;
}

.menu__link:hover {
  border-bottom: 1px solid #f8f8f8;
  transition: all .3s;
}

.menu__basket-link:after {
  content: "0";
  color: #f8f8f8;
  letter-spacing: .155em;
  text-align: center;
  background-color: red;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  margin-left: 8px;
  padding-top: 3px;
  font-family: Bebas Neue, sans-serif;
  font-size: 9px;
