.potr-team {
  margin-top: 100px;

  &__container {
    @include on-tablet {
      overflow: hidden;
      padding-bottom: 65px;
    }

    @include on-desktop {
      padding-bottom: 170px;
    }
  }

  &__wrapper {
    position: relative;
    padding-bottom: 40px;

    @include on-tablet {
      min-height: 570px;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-family: Bebas Neue, sans-serif;
    font-size: 58px;
    font-weight: 400;
    line-height: 70px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #333;
  }

  &__photo {
    background-image: url(../images/team-foto-mb.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-inline: -30px;
    height: 240px;
    cursor: pointer;
    transition: 0.3s;

    @include on-tablet {
      background-image: url(../images/team-foto-tab.png);
      grid-column: 4 /-1;
      margin: 0;
    }

    @include on-desktop {
      background-image: url(../images/team-foto-ds.png);
      grid-column: 5 /12;
      height: auto;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__rectangle {
    background-image: url(../images/team-rect-mb.png);
    background-repeat: no-repeat;
    position: absolute;
    width: -webkit-fill-available;
    bottom: 0;
    margin-inline: -30px;
    height: 295px;
    z-index: -1;

    @include on-tablet {
      background-image: url(../images/team-rect-tab.png);
      bottom: auto;
      top: 170px;
    }

    @include on-desktop {
      background-image: url(../images/team-rect-ds.png);
      top: 190px;
      margin-inline: -135px;
      width: 470px;
      height: 315px;
    }
  }

  &__content {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__text {
    margin-top: 30px;
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #4f4f4f;

    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 1;
      margin-top: 40px;
    }

    @include on-desktop {
      grid-column: 1 /5;
      margin-top: 196px;
    }
  }

  &__name {
    font-family: Bebas Neue, sans-serif;
    font-size: 231px;
    font-weight: 400;
    line-height: 278px;
    letter-spacing: 0.155em;
    text-align: left;
    -webkit-text-stroke: 0.81px #03839e;
    color: transparent;
    display: none;

    @include on-tablet {
      display: block;
      margin: 0;
    }

    @include on-desktop {
      font-family: Bebas Neue;
      font-size: 288px;
      line-height: 346px;
      -webkit-text-stroke: 1.2px #03839e;

    }
  }

  &__name-1 {
    height: 210px;
    position: relative;
    margin: 0 0 -40px -76px;

    @include on-desktop {
      margin: 0 0 -35px -65px;
      height: 155px;
    }
  }

  &__name-2 {
    position: absolute;
    right: 0;
    bottom: -122px;
    margin-right: -32px;
    letter-spacing: -4.5px;

    @include on-desktop {
      bottom: -255px;
      margin-right: -120px;
    }
  }
}
