.materials {
  margin-top: 100px;
  position: relative;
  margin-bottom: 100px;

  @include on-tablet {
    margin-top: 30px;
  }

  @include on-desktop {
    margin-top: 0;
  }

  &__title {
    margin: 0;
    font-family: Bebas Neue, sans-serif;
    font-size: 58px;
    font-weight: 400;
    line-height: 70px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #03839e;

    @include on-tablet {
      width: 550px;
      font-size: 80px;
      line-height: 96px;
      letter-spacing: 0.12em;
    }

    @include on-desktop {
      width: 880px;
      font-size: 139px;
      line-height: 167px;
      letter-spacing: 0.12em;
    }
  }

  &__quality {
    display: none;

    @include on-tablet {
      display: block;
      position: absolute;
      right: 0;
      top: 122px;
      width: 47%;
    }

    @include on-desktop {
      width: 37%;
    }
  }

  &__quality-title {
    font-family: Manrope, sans-serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: #4f4f4f;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__quality-text {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    padding-right: 30px;
  }

  &__photo {
    background-image: url(../images/materials-foto-mb.png);
    background-repeat: no-repeat;
    height: 240px;
    background-size: cover;
    background-position: center;
    margin-top: -75px;
    margin-inline: -30px;

    @include on-tablet {
      background-image: url(../images/materials-foto-tab.png);
      height: 576px;
      margin-top: 65px;
      margin-inline: -34px;
    }

    @include on-desktop {
      background-image: url(../images/materials-foto-ds.png);
      height: 1080px;
      margin-inline: -135px;
    }
  }

  &__extra {
    display: none;

    @include on-tablet {
      display: block;
    }
  }

  &__extra-1 {
    background-image: url(../images/elements/Component+.png);
    background-repeat: no-repeat;
    position: absolute;
    width: 51px;
    height: 51px;
    bottom: 12%;
    left: 11%;

    @include on-desktop {
      bottom: 11%;
      left: 16%;
    }
  }

  &__extra-2 {
    background-image: url(../images/elements/Component+.png);
    background-repeat: no-repeat;
    position: absolute;
    width: 51px;
    height: 51px;
    bottom: 27%;
    left: 30%;

    @include on-desktop {
      bottom: 28%;
      left: 38%;
    }
  }

  &__extra-3 {
    background-image: url(../images/elements/Component+.png);
    background-repeat: no-repeat;
    position: absolute;
    width: 51px;
    height: 51px;
    bottom: 8%;
    left: 44%;

    @include on-desktop {
      bottom: 8%;
      left: 48%;
    }
  }

  &__extra-4 {
    background-image: url(../images/elements/Component+.png);
    background-repeat: no-repeat;
    position: absolute;
    width: 51px;
    height: 51px;
    bottom: 17%;
    right: 37%;

    @include on-desktop {
      bottom: 15%;
      right: 38%;
    }
  }

  &__extra-5 {
    background-image: url(../images/elements/Component-.png);
    background-repeat: no-repeat;
    position: absolute;
    width: 51px;
    height: 51px;
    bottom: 300px;
    right: 160px;
  }

  &__description {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: -48px;
    margin-inline: -21px;
    padding: 30px 50px;
    background-color: #515151;
    color: #f8f8f8;
    opacity: 0.9;

    @include on-tablet {
      margin: 0;
      position: absolute;
      bottom: 55px;
      right: 30px;
      width: 210px;
      height: 180px;
      padding: 25px 40px;
    }

    &::after {
      content: '';
      background-image: url(../images/elements/materials-aft.png);
      background-repeat: no-repeat;
      display: block;
      width: 80px;
      height: 14px;
      margin-left: 37%;
      margin-top: 16px;

      @include on-tablet {
        display: none;
      }
    }
  }

  &__text-bold {
    font-weight: 700;
  }

  &__side-mark-right {
    background-image: url(../images/elements/side-mark-r.png);
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    right: 8px;
    width: 14px;
    height: 144px;
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__side-mark-left {
    background-image: url(../images/elements/side-mark-r.png);
    background-repeat: no-repeat;
    position: absolute;
    top: 198px;
    left: 8px;
    width: 14px;
    height: 144px;
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }
}
