.benefits {
  position: relative;
  margin: 100px 0;

  @include on-desktop {
    margin: 100px 0 200px;
  }

  &__content {
    @include page-grid;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../images/benefits-image.png);
    background-repeat: no-repeat;
    height: 828px;
    width: 633px;
    display: none;

    @include on-desktop {
      display: block;
      grid-column: 1 /6;
    }
  }

  &__use {
    @include on-tablet {
      padding-left: 20px;
    }
  }

  &__use-1 {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-desktop {
      grid-column: 6 /9;
      margin-top: 190px;
    }
  }

  &__use-2 {
    grid-column: 1 /-1;
    margin-top: 40px;

    @include on-tablet {
      grid-column: 1 /4;
    }

    @include on-desktop {
      grid-column: 9 /-1;
      margin-top: 190px;
    }
  }

  &__use-3 {
    grid-column: 1 /-1;
    margin-top: 40px;

    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 2;
    }

    @include on-desktop {
      grid-column: 6 /10;
    }
  }

  &__use-title {
    margin: 0;
    font-family: Manrope, sans-serif;
    font-size: 32px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 0em;
    text-align: left;
    color: #333;

    &::before {
      content: '';
      background-image: url(../images/elements/benefits-before.png);
      background-repeat: no-repeat;
      display: inline-block;
      width: 14px;
      height: 18px;
      margin-left: -20px;
      padding-right: 5px;
    }

    @include on-tablet {
      font-size: 36px;
      line-height: 54px;
    }
  }

  &__use-text {
    margin: 0;
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #828282;
  }

  &__button {
    grid-column: 1 /-1;
    margin-top: 40px;
    width: 240px;
    height: 48px;
    border: 1px;
    background-color: #ffe339;
    background-image: url(../images/icons/header-button.png);
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background-color: #ffec7a;
    }

    @include on-tablet {
      grid-column: 4 /-1;
      grid-row: 2;
      margin-top: 26px;
      background-color: transparent;
      border: 1px solid #03839e;

      &:hover {
        background-color: #03839e;
        background-image: url(../images/icons/button-hov.png);
      }
    }

    @include on-desktop {
      grid-column: 6 /9;
      grid-row: 3;
    }
  }

  &__use-text-3 {
    @include on-desktop {
      margin-right: -116px;
    }
  }

  &__discount {
    grid-column: 1 /-1;
    margin-top: 40px;

    @include on-tablet {
      grid-column: 4 /6;
      margin-top: -26px;
    }

    @include on-desktop {
      grid-column: 7 /-1;
      margin-top: 40px;
    }
  }

  &__discount-percent {
    font-family: Bebas Neue, sans-serif;
    font-size: 36px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #eb5757;

    @include on-tablet {
      font-size: 24px;
      line-height: 24px;
    }
  }

  &__discount-condition {
    margin-top: 10px;
    font-family: Bebas Neue, sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #03839e;

    @include on-tablet {
      font-size: 18px;
      line-height: 18px;
    }
  }

  &__side-mark {
    background-image: url(../images/elements/benef-side-mark.png);
    background-repeat: no-repeat;
    position: absolute;
    width: 14px;
    height: 211px;
    top: 0;
    right: 10px;
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }
}
