.for-whom {
  margin: 120px 0 120px;
  position: relative;

  @include on-tablet {
    margin-bottom: 140px;
  }

  @include on-desktop {
    margin: 100px 0 160px;
  }

  &__wrapper {
    @include on-desktop {
      @include page-grid;
    }
  }

  &__card {
    @include page-grid;
    margin-bottom: 70px;

    @include on-tablet {
      margin-bottom: 100px;
    }
  }

  &__card--1 {
    @include on-desktop {
      grid-column: 1 /-1;
    }
  }

  &__card--2 {
    @include on-desktop {
      display: flex;
      flex-direction: column;
      grid-column: 2 /5;
      padding-top: 50px;
      margin-bottom: 0;
    }
  }

  &__card--3 {
    @include on-desktop {
      display: flex;
      flex-direction: column;
      grid-column: 8 /-1;
      margin-top: -50px;margin-bottom: 0;
    }
  }

  &__title {
    font-family: Bebas Neue;
    font-size: 36px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #333;

    @include on-tablet {
      margin-top: 0;
    }
  }

  &__photo-1 {
    display: none;
    height: 284px;
    background-image: url(../images/whom1-tab.png);
    background-repeat: no-repeat;
    transition: 0.3s;
    cursor: pointer;

    @include on-tablet {
      display: block;
      grid-column: 1 /4;
    }

    @include on-desktop {
      background-image: url(../images/whom1-ds.png);
      height: 500px;
      grid-column: 1 /6;
      margin-right: -30px;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__content-1 {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: 4 /-1;
    }

    @include on-desktop {
      grid-column: 8 /-1;
      margin: 155px 30px 0 0;
    }
  }

  &__photo-2 {
    display: none;
    height: 417px;
    background-image: url(../images/whom2.png);
    background-repeat: no-repeat;
    transition: 0.3s;
    cursor: pointer;

    @include on-tablet {
      display: block;
      background-position-x: 30px;
      grid-column: 4 /-1;
    }

    @include on-desktop {
      order: 1;
      margin-inline: -30px;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__content-2 {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: 1 /4;
    }

    @include on-desktop {
      order: 2;
      margin-right: -105px;
    }
  }

  &__photo-3 {
    display: none;
    height: 405px;
    background-image: url(../images/whom3-tab.png);
    background-repeat: no-repeat;
    transition: 0.3s;
    cursor: pointer;

    @include on-tablet {
      display: block;
      grid-column: 1 /4;
    }

    @include on-desktop {
      background-image: url(../images/whom3-ds.png);
      height: 670px;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__content-3 {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: 4 /-1;
    }
  }

  &__card-mark {
    display: none;

    @include on-tablet {
      display: block;
    }

    &::before {
      content: '';
      background-image: url(../images/elements/before-sighn.png);
      background-repeat: no-repeat;
      position: absolute;
      width: 11px;
      height: 11px;
      left: 48%;
    }
  }

  &__card-mark-1 {
    margin: -20px 0 20px;

    @include on-desktop {
      &::before {
        left: 55%;
      }
    }
  }

  &__card-mark-2 {
    margin-top: 30px;
    margin-left: 82%;
    margin-bottom: 30px;

    @include on-desktop {
      margin-left: 0;
    }

    &::before {
      left: 38%;

      @include on-desktop {
        left: 14%;
      }
    }
  }

  &__card-mark-3 {

    @include on-desktop {
      margin-top: 30px;
    }

    &::before {

      @include on-desktop {
        left: 55%;
      }
    }
  }

  &__card-number-1 {
    background-image: url(../images/elements/card-1.png);
    background-repeat: no-repeat;
    display: block;
    width: 41px;
    height: 46px;

    @include on-tablet {
      position: absolute;
      margin-left: -55px;
    }

    @include on-desktop {
      margin-left: -6%;
    }
  }

  &__card-number-2 {
    background-image: url(../images/elements/card-2.png);
    background-repeat: no-repeat;
    display: block;
    width: 41px;
    height: 46px;
  }

  &__card-number-3 {
    background-image: url(../images/elements/card-3.png);
    background-repeat: no-repeat;
    display: block;
    width: 41px;
    height: 46px;

    @include on-tablet {
      position: absolute;
      margin-left: -55px;
      margin-top: 14px;
    }
  }

  &__card-title {
    margin: 16px 0;
    font-family: Bebas Neue, sans-serif;
    font-size: 36px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0.155em;
    color: #000;
    text-align: left;

    &::after {
      content: '';
      background-image: url(../images/elements/title-after.png);
      background-repeat: no-repeat;
      position: absolute;
      width: 41px;
      height: 46px;
      opacity: 0;

      @include on-tablet {
        opacity: 1;
      }
    }
  }

  &__card-description {
    margin-bottom: 0;
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #4f4f4f;
  }

  &__side-mark-right {
    background-image: url(../images/elements/side-mark-r.png);
    background-repeat: no-repeat;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 22px;
    height: 140px;

    @include on-tablet {
      bottom: auto;
      top: -120px;
    }

    @include on-desktop {
      top: 0;
    }
  }

  &__side-mark-left {
    background-image: url(../images/elements/side-mark-l.png);
    background-repeat: no-repeat;
    display: none;
    width: 22px;
    height: 140px;

    @include on-desktop {
      display: block;
      position: absolute;
      left: 134px;
      bottom: 0;
    }
  }
}
