.questions {
  position: relative;
  height: 840px;

  @include on-tablet {
    height: 386px;
  }

  &__background {
    position: absolute;
    width: 100%;
    max-height: 1003px;
    z-index: -1;
  }

  &__background-mb {
    @include on-tablet {
      display: none;
    }
  }

  &__background-tab {
    display: none;

    @include on-tablet {
      display: block;
      height: 500px;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__background-ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 90%;
    }
  }

  &__side-mark {
    background-image: url(../images/elements/quest-side-mark-mb.png);
    background-repeat: no-repeat;
    position: absolute;
    width: 150px;
    height: 170px;
    top: -74px;
    right: 0;

    @include on-tablet {
      background-image: url(../images/elements/quest-side-mark-tab.png);
      top: -105px;
      width: 134px;
    }

    @include on-desktop {
      background-image: url(../images/elements/quest-side-mark-ds.png);
      top: -94px;
      right: 60px;
      width: 160px;
    }
  }

  &__wrapper {
    padding-top: 50px;

    @include on-tablet {
      @include page-grid;
      padding-top: 20px;
    }
  }

  &__title {
    margin: 0;
    font-family: Bebas Neue, sans-serif;
    font-size: 36px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0.155em;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /5;
      grid-row: 1;
    }

    @include on-desktop {
      grid-column: 1 /6;
    }
  }

  &__description {
    margin: 0;
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 1;
      margin-top: 45px;
    }

    @include on-desktop {
      grid-column: 1 /5;
    }
  }

  &__form-title {
    margin: 34px 0 24px;
    font-family: Manrope, sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 36px;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 1;
      margin-top: 96px;
    }
  }

  &__form {
    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 1;
      margin-top: 160px;
    }
  }

  &__input {
    border: none;
    background-color: transparent;
    height: 30px;
    border-bottom: 1px solid #0000004d;
    outline: none;
    margin-bottom: 10px;
    width: 100%;
    cursor: pointer;
    transition: 0.3s;

    &:focus {
      &::placeholder {
        color: #03839e;
      }
    }

    &:hover {
      border-bottom: 1px solid #03839e;
    }

    @include on-tablet {
      width: 340px;
    }
  }

  &__message {
    resize: none;
    line-height: 30px;
  }

  &__button {
    grid-column: 1 /-1;
    margin-top: 20px;
    width: 100%;
    height: 48px;
    border: 1px;
    background-color: #03839e;
    background-image: url(../images/icons/submit-button.png);
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background-color: #04a5c5;
      transition: 0.3s;
    }

    @include on-tablet {
      width: 260px;
    }
  }

  &__contacts {
    margin-top: 48px;

    @include on-tablet {
      grid-column: 5 /-1;
      grid-row: 1;
      margin-top: 24px;
    }

    @include on-desktop {
      grid-column: 7 /10;
    }
  }

  &__contact {
    margin-top: 40px;

    @include on-tablet {
      margin-top: 20px;
    }
  }

  &__contacts-title {
    font-family: Manrope, sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 36px;
    text-align: left;
    color: #00000063;
  }

  &__contact-value {
    text-decoration: none;
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: #000;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
      color: blue;
    }
  }
}
