.potr-size {
  &__article {
    @include page-grid;
  }

  &__title {
    grid-column: 1 /-1;
    font-family: Bebas Neue, sans-serif;
    font-size: 36px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #000;
  }

  &__description {
    grid-column: 1 /-1;
    margin: 0 0 30px;
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #4f4f4f;

    @include on-tablet {
      grid-column: 1 /6;
    }

    @include on-desktop {
      grid-column: span 3;
    }
  }

  &__image {
    grid-column: 1 /-1;
    background-image: url(../images/potr-size-mb.png);
    background-repeat: no-repeat;
    height: 260px;
    background-position: center;
    margin-inline: -30px;

    @include on-tablet {
      grid-column: 4 /-1;
      background-image: url(../images/potr-size-tab.png);
      height: 337px;
      margin-left: -112px;
    }

    @include on-desktop {
      grid-column: 7 /-1;
      background-image: url(../images/potr-size-ds.png);
      height: 427px;
      margin-left: -30px;
    }
  }

  &__example {
    grid-column: 1 /-1;

    @include on-desktop {
      grid-column: 4 /7;
    }
  }

  &__example-1 {
    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 3;
      padding-right: 90px;
    }

    @include on-desktop {
      grid-row: 2;
      padding: 0;
    }
  }

  &__example-2 {
    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 3;
      padding: 124px 90px 0 0;
    }

    @include on-desktop {
      grid-row: 2;
      padding: 124px 0 0;
    }
  }

  &__example-title {
    margin: 0;
    font-family: Bebas Neue, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.155em;
    text-align: left;
    color: #000;
  }

  &__example-sizes, &__addition {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #4f4f4f;
  }

  &__addition {
    grid-column: 1 /-1;

    @include on-desktop {
      grid-column: 1 /7;
      grid-row: 3;
      margin: -100px 0 0 12px;
    }

    &::before {
      content: '';
      background-image: url(../images/elements/size-before.png);
      background-repeat: no-repeat;
      display: inline-block;
      width: 6px;
      height: 14px;
      margin-left: -12px;
    }
  }
}
