.features {

  &__wrapper {
    @include page-grid;
  }

  &__title {
    margin: 0;
    grid-column: 1 /-1;
    font-family: Bebas Neue, sans-serif;
    font-size: 58px;
    font-weight: 400;
    line-height: 70px;
    letter-spacing: 0.155em;
    text-align: left;

    @include on-tablet {
      margin-bottom: 50px;
    }

    @include on-desktop {
      margin-bottom: 80px;
    }
  }

  &__title-colored {
    color: #03839e;
  }

  &__title-transparent {
    color: transparent;
    -webkit-text-stroke: 0.68px #03839e;
  }

  &__card-1 {
    margin-top: 30px;
    grid-column: 1 /-1;
    text-align: center;

    @include on-tablet {
      grid-column: 3 /5;
      margin: 0;
    }

    @include on-desktop {
      grid-column: 1 /5;
    }
  }

  &__photo-1 {
    background-image: url(../images/feat-1-mb.png);
    background-repeat: no-repeat;
    background-position: center center;
    height: 260px;
    cursor: pointer;
    transition: 0.3s;

    @include on-tablet {
      background-image: url(../images/feat-1-tab.png);
      height: 220px;
      background-size: cover;
    }

    @include on-desktop {
      background-image: url(../images/feat-1-ds.png);
      height: 370px;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__card-2 {
    margin-top: 70px;
    grid-column: 1 /-1;
    text-align: center;

    @include on-tablet {
      grid-column: 5 /-1;
      margin: 0;
    }

    @include on-desktop {
      grid-column: 5 /9;
    }
  }

  &__photo-2 {
    background-image: url(../images/feat-2-mb.png);
    background-repeat: no-repeat;
    background-position: center center;
    height: 260px;
    cursor: pointer;
    transition: 0.3s;

    @include on-tablet {
      background-image: url(../images/feat-2-tab.png);
      height: 220px;
      background-size: cover;
    }

    @include on-desktop {
      background-image: url(../images/feat-2-ds.png);
      height: 370px;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__card-3 {
    margin-top: 70px;
    grid-column: 1 /-1;
    text-align: center;

    @include on-tablet {
      grid-column: 1 /3;
      grid-row: 2;
      margin: 0;
    }

    @include on-desktop {
      grid-column: 9 /-1;
    }
  }

  &__photo-3 {
    background-image: url(../images/feat-3-mb.png);
    background-repeat: no-repeat;
    background-position: center center;
    height: 260px;
    cursor: pointer;
    transition: 0.3s;

    @include on-tablet {
      background-image: url(../images/feat-3-tab.png);
      height: 220px;
      background-size: cover;
    }

    @include on-desktop {
      background-image: url(../images/feat-3-ds.png);
      height: 370px;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__card-title {
    margin: 30px 0;
    font-family: Bebas Neue, sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.155em;
    text-align: center;

    @include on-tablet {
      text-align: left;
    }

    @include on-desktop {
      text-align: center;
    }
  }

  &__card-description {
    margin: 0;
    padding-inline: 16px;
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;

    @include on-tablet {
      text-align: left;
      padding-inline: 0 16px;
    }

    @include on-desktop {
      text-align: center;
      padding: 0;
    }
  }
}
