.nav {
  position: absolute;
  top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  @include on-tablet {
    top: 30px;
  }

  &__icon {
    transition: 0.3s;
    &:hover {
      transform: scale(1.03);
      transition: 0.3s;
    }
  }

  &__links {
    margin-left: 200px;
    display: flex;
    width: 60%;
    justify-content: space-between;
    align-items: center;
    display: none;

    @include on-desktop {
      display: flex;
    }
  }

  &__link {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #4f4f4f;
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      transform: scale(1.03);
      color: #6e9fcc;
      transition: 0.3s;
    }
  }

  &__menu {
    @include on-desktop {
      display: none;
    }
  }

  &__card {
    margin-left: 100px;
    transition: 0.3s;

    &:hover {
      transform: scale(1.05);
      transition: 0.3s;
    }
  }
}
